/* pf3 modules */
@import url('~patternfly/dist/css/patternfly.css');
@import url('~patternfly/dist/css/patternfly-additions.css');
@import url('~patternfly-react/dist/css/patternfly-react.css');

/* selective pf4 base styles */
@import url('~@patternfly/patternfly/patternfly-variables.css');
@import url('~@patternfly/patternfly/patternfly-shield-noninheritable.css');
@import url('~@patternfly/patternfly/patternfly-common.css');
@import url('~@patternfly/patternfly/patternfly-globals.css');
@import url('~@patternfly/patternfly/utilities/Spacing/spacing.css');

/* selective pf4 component specific stylesheets */
@import url('~@patternfly/patternfly/components/Page/page.css');
@import url('~@patternfly/patternfly/components/Nav/nav.css');
@import url('~@patternfly/patternfly/components/Dropdown/dropdown.css');
@import url('~@patternfly/patternfly/components/AboutModalBox/about-modal-box.css');
@import url('~@patternfly/patternfly/components/BackgroundImage/background-image.css');
@import url('~@patternfly/patternfly/components/Content/content.css');
@import url('~@patternfly/patternfly/components/Popover/popover.css');
@import url('~@patternfly/patternfly/components/Tooltip/tooltip.css');

:root {
  --pf-global--link--Color: #0088ce;
  --pf-c-nav__list-link--m-current--after--BackgroundColor: #0088ce;
  --pf-global--link--Color--hover: #00659c;
  --pf-c-backdrop--Color: rgba(3,3,3,.62);

}

.pf-c-dropdown {
  --pf-c-dropdown__toggle--FontSize: var(--pf-global--FontSize--xs);
}

.pf-c-page {
  width: 100%;
  height: 100vh;
}

.pf-c-dropdown__menu-item {
  --pf-c-dropdown__menu-item--FontSize: var(--pf-global--FontSize--xs);
}

body {
  font-size: var(--pf-global--FontSize--sm);
  line-height: var(--pf-global--LineHeight--sm);
  color: var(--pf-global--Color--100);
}

#root {
  flex: 1;
  display: flex;
}

.navbar-pf .navbar-utility > li > button {
  padding: 9px 10px;
}

.blank-slate-pf .blank-slate-pf-secondary-action a {
  padding: 2px 6px;
  margin-bottom: 0;
}

.wizard-pf-footer .wizard-pf-extrabuttons {
  order: -1;
  margin-right: 25px;
}

.wizard-pf-footer .wizard-pf-cancel {
  order: -2;
}

.modal-body > div {
  width: 100%;
}

.list-view-pf-stacked .list-group-item-heading,
.label {
  font-size: var(--pf-global--FontSize--xs);
}

.syn-scrollable {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.syn-scrollable--body {
  flex: 1 1 auto;
  overflow: auto;
}

.pf-c-backdrop {
  background-color: var(--pf-c-backdrop--Color);
}

.pf-c-about-modal-box__content dd {
  word-break: break-word;
}

.pf-c-content {
  --pf-c-content--FontSize: var(--pf-global--FontSize--sm);
}

.pf-c-dropdown__toggle.syn-help-dropdown {
  --pf-c-dropdown__toggle--FontSize: var(--pf-global--FontSize--md);
}

.list-view-pf-view {
  margin-top: 0;
}

.connection-card,
.connection-card:hover {
  color: inherit;
  text-decoration: none;
}

.connection-card__content {
  display: flex;
  align-items: center;
  flex-flow: column;
  text-align: center;
}

.connection-card__heading--tech-preview {
  color: #363636;
  cursor: pointer;
  font-size: small;
  padding: 4px;
}

.connection-card__heading--no-border {
  border-bottom: 1px solid #FFFF;
  font-size: small;
  padding: 4px;
}

.connection-card__footer--tech-preview {
  background-color: #fdf2e5;
  border-color: #ec7a08;
  font-size: smaller;
  text-align: center;
  padding: 15px;
}

.connection-details-header__connectionName {
  font-size: 2em;
}

.connection-details-header__connectionIcon {
  width: 46px;
}

.connection-details-header__row {
  margin-top: 20px;
  margin-bottom: 20px;
}

.connection-details-header__propertyLabel {
  font-size: 1.25em;
  font-weight: bold;
  text-align: right;
}

.connection-details-header__propertyValue {
  font-size: 1.25em;
  text-align: left;
}

.connection-details-form__buttonBar {
  border-top: none;
  margin-left: auto;
  padding: 0px 20px 10px;
  width: 90%;
}

.connection-details-form__editButton {
  margin-bottom: 8px;
  margin-right: 4px;
}

.connection-details-form__formFields {
  margin-left: auto;
  width: 90%;
}

.api-connector-card {
  color: inherit;
  display: flex;
  justify-content: center;
  text-align: center;
  text-decoration: none;
}

.api-connector-card__icon {
  max-height: 50px;
  max-width: 50px;
}

.api-connector__title {
  font-weight: 400;
}

.extension-detail__extensionId {
  font-size: 0.5em;
  margin-left: 10px;
}

.extension-detail__extensionTitle {
  margin: 0;
}

/* .extension-detail__sectionHeading {
} */

.extension-detail__titleButtons button + button {
  margin-left: 4px;
}

.extension-import-review__buttonBar {
  margin: 12px;
}

.extension-import-review__cancelButton {
  margin-left: 4px;
}

.extension-import-review__container {
  background: white;
  width: 100%;
}

.extension-import-review__propertyLabel {
  font-weight: bold;
  text-align: right;
}

.extension-import-review__propertyValue {
  text-align: left;
}

.extension-import-review__title {
  font-size: 1.5em;
  padding: 12px;
  text-align: left;
}

.extension-overview__propertyLabel {
  font-weight: bold;
  text-align: right;
}

.extension-overview__propertyValue {
  text-align: left;
}

.TopIntegrations .list-view-pf .list-group-item {
  border: solid 0 !important;
}

.Dashboard-header {
  display: flex;
  flex-flow: row nowrap;
}

.Dashboard-header__title {
  flex: 1;
}

.Dashboard-header__actions {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  margin-top: 20px; /* to align it with the title */
  margin-bottom: 10px; /* to align it with the title */
}

.Dashboard-header__actions > * {
  margin-left: 10px;
}

.integration-editor-layout {
  flex: 1 0;
  display: flex;
  flex-flow: column;
}

.integration-editor-layout__sidebarOuter {
  display: none;
}

.integration-editor-layout__body {
  display: flex;
  flex: 1 0;
}

.integration-editor-layout__contentOuter {
  display: flex;
  flex-flow: column;
}

.integration-editor-layout__sectionLight,
/* hack to make the connections toolbar match the header */
.integration-editor-layout__contentOuter .toolbar-pf {
  background-color: var(--pf-global--BackgroundColor--200);
}

@media (min-width: 768px) {
  .integration-editor-layout {
    height: 100%;
    display: flex;
    flex-flow: column;
  }

  .integration-editor-layout__sidebarOuter {
    background-color: var(--pf-global--BackgroundColor--200);
    box-shadow: rgba(3, 3, 3, 0.07) 12px 0 10px -4px;
    flex: 0 0 auto;
    display: flex;
    overflow: hidden;
    z-index: 1;
  }
  .integration-editor-layout__sidebarInner {
    background-color: var(--pf-global--BackgroundColor--200);
    overflow-x: hidden;
    overflow-y: auto;
    display: flex;
  }

  .integration-editor-layout__header {
    border-bottom: 1px solid #ededed;
  }

  .integration-editor-layout__body {
    flex: 1 0;
    display: flex;
    width: 100%;
    overflow: hidden;
  }

  .integration-editor-layout__contentOuter {
    overflow: auto;
    padding: 0;
    width: 100%;
  }

  .integration-editor-layout__contentInner {
    flex: 1;
    display: flex;
    flex-flow: column;
  }
}



.integration-editor-wizard {
  height: calc(100% - 36px - 60px);
}

.integration-editor-wizard__body {
  max-height: none;
}

.integration-editor-wizard.has-footer .integration-editor-wizard__body {
  height: calc(100vh - 60px - 36px - 57px - 61px);
  max-height: none;
}

@media (min-width: 768px) {
  .integration-editor-wizard__body {
    height: calc(100vh - 121px - 60px - 36px);
    max-height: none;
  }

  .integration-editor-wizard.has-footer .integration-editor-wizard__body {
    height: calc(100vh - 121px - 60px - 36px - 61px);
    max-height: none;
  }
}

.integration-editor-wizard__contentWrapper {
  overflow: auto;
  padding: 0;
}

.integration-editor-wizard__content {
  max-width: 900px;
  margin: auto;
}

.integration-editor-wizard__footer {
  padding: 17px 15px;
  z-index: 1;
}

.integration-flow-add-step {
  display: flex;
  z-index: 2;
  position: relative;
  justify-content: center;
  margin: 5px 0;
}

.integration-flow-add-step__iconWrapper {
  display: flex;
  justify-content: center;
  position: relative;
}

.integration-flow-add-step__iconWrapper::before {
  content: '';
  position: absolute;
  width: 2px;
  background-color: #d4d4d4;
  top: -5px;
  bottom: -5px;
  left: calc(100% / 2);
  z-index: -1;
}


.integration-flow-add-step__icon {
  color: #158acc;
  font-size: 24px;
  display: inline;
}

.integration-flow-add-step__icon .fa {
  background-color: #fff;
}

.integration-flow-add-step__links .popover-content {
  display: flex;
  flex-flow: column;
}

.integration-flow-add-step__links .popover-content a {
  margin: 3px 0;
}


.integration-flow-step-details {
  width: 100%;
  margin-top: 15px;
  margin-bottom: 40px;
}

.integration-flow-step-details__title {
  background-color: #d1d1d1;
  color: #fff;
  padding: 2px 20px;
}

.integration-flow-step-details.is-active .integration-flow-step-details__title {
  background-color: #158acc;
  color: #fff;
}

.integration-flow-step-details__body {
  padding: 10px 20px;
}

.integration-flow-step-details.is-active .integration-flow-step-details__body {
  background-color: #d6eefa;
}

.integration-flow-step-details__overview {
  display: flex;
  margin: 0;
}

.integration-flow-step-details__overview dt {
  flex: 0 0 40%;
}

.integration-flow-step-details__overview dd {
  flex: 1 1 0;
}
.integration-editor-steps-list {
  background: transparent;
  max-width: 900px;
  margin: auto;
}

.integration-flow-step {
  display: flex;
  margin: 20px 0;
}
.integration-flow-step:last-child {
  align-items: flex-start; /* to cut the vertical line at the end of the icon, and not of the whole container */
}

.integration-flow-step__iconWrapper {
  display: flex;
  justify-content: center;
  position: relative;
  flex: 0 0 155px;
  z-index: 1;
}

.integration-flow-step:not(:first-child) .integration-flow-step__iconWrapper::before {
  content: '';
  position: absolute;
  width: 2px;
  background-color: #d4d4d4;
  top: -20px; /* same as root container top margin */
  bottom: 0;
  left: calc(100% / 2);
  z-index: 0;
}

.integration-flow-step:not(:last-child) .integration-flow-step__iconWrapper::after {
  content: '';
  position: absolute;
  width: 2px;
  background-color: #d4d4d4;
  top: 0;
  bottom: -20px; /* same as root container bottom margin */
  left: calc(100% / 2);
  z-index: 0;
}

.integration-flow-step__icon {
  border: 3px solid #d0d0d0;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  width: 55px;
  height: 55px;
  position: relative;
  z-index: 1;
}


.is-active .integration-flow-step__icon {
  border-color: #0088ce;
}
.integration-status-detail {
  align-items: center;
  display: inline-flex;
  flex-flow: row;
}

.integration-icon {
  display: flex;
  align-items: center;
}

.integration-icon__icon {
  width: 35px;
  height: 35px;
}
.integration-icon__divider {
  margin: 0 10px;
  font-size: 18px;
}
.integration-vertical-flow {
  min-width: 185px;
  display: flex;
  flex-flow: row nowrap;
  flex: 1;
}

.integration-vertical-flow.is-expanded {
  min-width: 400px;
}

.integration-vertical-flow__body {
  flex: 1 1 auto;
}

.integration-vertical-flow__expand {
  flex: 0 0 30px;
}

.integration-vertical-flow__expand .btn {
  width: 100%;
  height: 100%;
}

.integration-vertical-flow__expand .btn:focus {
  outline: none;
}

.integration-vertical-flow__expand .btn::before {
  content: '\00bb';
}

.is-expanded .integration-vertical-flow__expand .btn::before {
  content: '\00ab';
}

.integration-steps-horizontal-item {
  height: 100px;
  width: 120px;
  display: inline-block;
  text-align: center;
  vertical-align: top;
  position: relative;
  padding: 0 10px;
}

.integration-steps-horizontal-item * {
 vertical-align: center;
}

.integration-steps-horizontal-item a {
  color: var(--pf-global--Color--100);
}

.integration-steps-horizontal-item a:hover {
  text-decoration: none;
}

.integration-steps-horizontal-item .step-icon {
  border-radius: 100px;
  width: 55px;
  height: 55px;
  margin: 0 auto 5px;
  background-color: inherit;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 3px solid #0088ce;
}
.integration-steps-horizontal-item .step-icon img {
  max-width: 24px;
  max-height: 24px;
}

.integration-steps-horizontal-item__arrow,
.integration-steps-horizontal-item__icon {
  padding: 0;
}

.integration-steps-horizontal-item__icon {
  width: 100px;
}

.integration-steps-horizontal-item .step-arrow {
  position: absolute;
  top: 28px;
  left: -8px;
  -webkit-transform: translate(50%, -50%);
  transform: translate(50%, -50%);
  font-size: 2em;
  line-height: 1;
  color: #72767b;
}

.dnd-file-chooser {
  border: 2px dashed #d1d1d1; /* color-pf-black-300 */
  cursor: pointer;
  width: 100%;
}

.dnd-file-chooser[disabled],
.dnd-file-chooser[disabled]:hover {
  color: lightgray;
  border: 2px dashed #d1d1d1; /* color-pf-black-300 */
  cursor: default;
}

.dnd-file-chooser:hover {
  border-color: #39a5dc; /* color-pf-blue-300 */
  background: #fafafa; /* color-pf-black-100 */
}

.dnd-file-chooser__helpText {
  margin: 20px;
}

.dnd-file-chooser__instructions {
  margin: 20px;
}

.dnd-file-chooser__selectedFile {
  font-style: italic;
}

.dnd-file-chooser__selectedFileLabel {
  font-weight: bold;
}

.dnd-file-chooser__notificationList {
  margin: inherit;
}

.dnd-file-chooser__uploadMessage {
  font-weight: bold;
}

.Loader.is-block {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
}

.Loader.is-inline {
  display: inline-flex;
  align-items: center;
}

.inline-text-editIcon {
  cursor: pointer;
  color: #1ea7fd;
  font-size: 0.75em;
  margin-left: 8px;
}

@media (max-width: 768px) {
  .toolbar-pf-actions > .toolbar-pf-filter,
  .toolbar-pf-actions > .form-group {
    float: left;
    margin-bottom: 0;
    margin-right: 5px;
  }
  .toolbar-pf-actions .toolbar-pf-action-right {
    clear: both;
    padding-top: 10px;
  }
}
.LogViewer {
  margin-bottom: 10px;
}

.LogViewerRow {
  font-family: monospace;
  display: flex;
  padding: 2px 0;
}

.LogViewerRow_number {
  color: #237893;
  flex-basis: 36px;
  text-align: right;
}

.LogViewerRow_content {
  line-height: 1.3;
  margin-left: 36px;
  flex: 1;
}

.progress-link {
  width: 180px;
}

.progress-link .progress-link-value {
  text-transform: capitalize;
}

.dv-connection-card,
.dv-connection-card:hover {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

.dv-connection-card__content {
  display: flex;
  align-items: center;
  flex-flow: column;
  text-align: center;
}

.dv-connection-card__status {
  margin-bottom: 10px;
}

.virtualization-publish-status-detail {
  align-items: center;
  display: inline-flex;
  flex-flow: row;
  margin-right: 10px;
}

.virtualization-publish-status-label {
  margin-right: 10px;
}

.views-create-layout {
  height: calc(100% - 36px - 60px);
}

.views-create-layout__body {
  max-height: none;
}

.views-create-layout.has-footer .views-create-layout__body {
  height: calc(100vh - 60px - 36px - 57px - 61px);
  max-height: none;
}

@media (min-width: 768px) {
  .views-create-layout__body {
    height: calc(100vh - 121px - 60px - 36px);
    max-height: none;
  }

  .views-create-layout.has-footer .views-create-layout__body {
    height: calc(100vh - 121px - 60px - 36px - 61px);
    max-height: none;
  }
}

.views-create-layout__contentWrapper {
  overflow: auto;
  padding: 0;
}

.views-create-layout__content {
  max-width: 1100px;
  margin: auto;
}

.views-create-layout__footer {
  padding: 17px 15px;
  z-index: 1;
}

.list-view-pf .list-group-item-heading {
  flex-basis: calc(50% - 20px);
}

.empty-views-import {
  margin-right: 10px;
}

.list-group-item-heading {
  margin-right: 30px;
}

.integration-detail__breadcrumb {
  background-color: #FFFFFF;
}

.integration-detail-activity-toolbar .integration-detail-activy-toolbar-last-refresh {
  color: #bbb;
}

.integration-detail-info {
    display: flex;
}

.integration-detail-info .fa-pencil {
    font-weight: 400;
    color: #0088ce;
    border-radius: 0;
    border: 1px solid transparent;
    padding: 2px 6px;
    font-size: 12px;
}

.integration-detail-activity-item {
  border: none;
}

.integration-detail-activity-item .list-view-pf-main-info {
  min-height: 68px;
}

.integration-detail-activity-item .integration-detail-activity-item__status-item {
  padding-top: 6px;
}

.integration-detail-activity-item .integration-detail-activity-item-step-output {
  border: none;
  padding-top: 2px;
}

.integration-detail-history-list-view {
    background-color: white;
}

.integration-detail-history-list-view__description {
    font-weight: bold;
    padding-top: 20px;
}

.integration-detail-history-list-view .list-view-pf-view {
    margin-top: 0;
}

.integration-detail-history-list-view .list-view-pf-left .list-view-pf-icon-sm {
  border: none;
  font-size: 0.8em;
}
